"use client";

import { useState } from "react";
import {
  PackageSearch,
  PackagePlus,
  ClipboardList,
  BarChart3,
} from "lucide-react";
import AlertaStock from "../../components/almacen/AlertaStock";
import EntradasSalidas from "../../components/almacen/EntradasSalidas";
import KardexHistorial from "../../components/almacen/KardexHistorial";
import ReporteAlmacen from "../../components/almacen/ReporteAlmacen";
import "../../../styles/almacen.css";

export default function GestionAlmacenPage() {
  type Vista = "alerta" | "movimientos" | "kardex" | "reporte";
  const [vistaActiva, setVistaActiva] = useState<Vista>("alerta");

  return (
    <div className="space-y-6">
      <div>
        <h2 className="text-3xl font-bold tracking-tight text-gray-800 flex items-center gap-2">
          🏬 Gestión de Almacén
        </h2>
        <p className="text-muted-foreground">
          Control de stock, movimientos y monitoreo.
        </p>
      </div>

      {/* Navegación visual estilo tabs */}
      <div className="grid w-full grid-cols-4 gap-3">
        <button
          onClick={() => setVistaActiva("alerta")}
          className={`flex items-center justify-center gap-2 px-4 py-3 font-semibold text-sm rounded-xl transition ${
            vistaActiva === "alerta"
              ? "bg-blue-600 text-white"
              : "bg-gray-100 text-gray-700 hover:bg-gray-200"
          }`}
        >
          <PackageSearch className="h-4 w-4" />
          Alerta de Stock
        </button>
        <button
          onClick={() => setVistaActiva("movimientos")}
          className={`flex items-center justify-center gap-2 px-4 py-3 font-semibold text-sm rounded-xl transition ${
            vistaActiva === "movimientos"
              ? "bg-blue-600 text-white"
              : "bg-gray-100 text-gray-700 hover:bg-gray-200"
          }`}
        >
          <PackagePlus className="h-4 w-4" />
          Entradas/Salidas
        </button>
        <button
          onClick={() => setVistaActiva("kardex")}
          className={`flex items-center justify-center gap-2 px-4 py-3 font-semibold text-sm rounded-xl transition ${
            vistaActiva === "kardex"
              ? "bg-blue-600 text-white"
              : "bg-gray-100 text-gray-700 hover:bg-gray-200"
          }`}
        >
          <ClipboardList className="h-4 w-4" />
          Kardex
        </button>
        <button
          onClick={() => setVistaActiva("reporte")}
          className={`flex items-center justify-center gap-2 px-4 py-3 font-semibold text-sm rounded-xl transition ${
            vistaActiva === "reporte"
              ? "bg-blue-600 text-white"
              : "bg-gray-100 text-gray-700 hover:bg-gray-200"
          }`}
        >
          <BarChart3 className="h-4 w-4" />
          Reporte
        </button>
      </div>

      {/* Contenido dinámico */}
      <div className="pt-4">
        {vistaActiva === "alerta" && <AlertaStock />}
        {vistaActiva === "movimientos" && <EntradasSalidas />}
        {vistaActiva === "kardex" && <KardexHistorial />}
        {vistaActiva === "reporte" && <ReporteAlmacen />}
      </div>
    </div>
  );
}
